  line-height: 19px; }

.button:hover {
  transform: scale(1.1); }

.navbar__btn-open {
  position: fixed;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  z-index: 100;
  padding: 0;
  border: none;
  background: transparent;
  font-family: inherit;
  cursor: pointer;
  background: #ff7931; }

.navbar__btn-open div {
  position: absolute;
  top: 50%;
  left: 50%;
  right: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 2px;
  background: #ffffff; }

.navbar__btn-open div:after, .navbar__btn-open div::before {
  content: "";
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  width: 30px;
  height: 2px;
  background: #ffffff; }

.navbar__btn-open div::after {
  top: 10px; }

.navbar__btn-open div::before {
  bottom: 10px; }

.navbar__btn-close {
  display: none;
  position: relative;
  margin: 15px 15px 30px auto;
  padding: 0;
  border: none;
  background: transparent;
  font-family: inherit;
  cursor: pointer;
  width: 30px;
  height: 30px; }

.navbar__btn-close:after, .navbar__btn-close:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 2px;
  height: 35px;
  background: rgba(0, 0, 0, 0.2); }

.navbar__btn-close:after {
  transform: rotate(45deg); }

.navbar__btn-close::before {
  transform: rotate(-45deg); }

.navbar__btn-close--active {
  display: block; }

.navbar__btn-close--active:hover {
  animation: rotate 0.2s ease-in-out; }

.navbar__list {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none; }

.navbar__list--active {
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  z-index: 100;
  background: #ffffff;
  animation: show 0.4s linear; }

.navbar__item {
  text-transform: uppercase; }

.navbar__link {
  position: relative;
  display: block;
  margin-bottom: 20px;
  padding: 18px 16px;
  text-decoration: none;
  font-size: 15px;
  line-height: 18px;
  color: #48494d;
  background: #f9f9fc; }

.navbar__link::after {
  content: "";
  position: absolute;
  display: block;
  top: 50%;
  right: 16px;
  width: 15px;
  height: 15px;
  box-sizing: border-box;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  border-right: 1px solid rgba(0, 0, 0, 0.2);
  transform: translateY(-50%) rotate(45deg); }

@media (min-width: 1200px) {
  .navbar__list {
    display: flex;
    justify-content: center;
    margin-top: 0; }
  .navbar__link {
    padding: 46px 0px 26px;
    margin-right: 50px;
    color: #000000;
    font-size: 15px;
    background: transparent; }
  .navbar__link::after {
    display: none; }
  .navbar__btn-close--active {
    display: none; }
  .navbar__btn-open,
  .navbar__btn-close {
    display: none; }
  .navbar__list--active {
    position: static; } }

@keyframes show {
  0% {
    top: -500px;
    opacity: 0.4; }
  100% {
    top: 0;
    opacity: 1; } }

@keyframes rotate {
  0% {
    transform: scale(0.3) rotate(0); }
  100% {
    transform: scale(1) rotate(360deg); } }

